/* image container style */

.home .img-container {
  margin: 1em 0 2em 0;
  display: flex;
  justify-content: space-evenly;
}
.home .img-container .card {
  width: 40em;
  text-align: center;
  padding: 1em;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.home .img-container .card p {
  color: #525252;
  font-size: 25px;
}

.home .img-container .card .role {
  font-size: 23px;
}

.home .img-container .img-figure img {
  border-radius: 5%;
  width: 23em;
  margin: auto;
  display: block;
  width: 400px;
  height: 300px;
}
.home .img-container .go {
  border-top: 2px solid #e6e6e6;
  padding: 0.5em;
}

