.home .img-container .card {
  margin: 1em auto;
}

.home span {
  font-size: 33px;
}

.home button {
  padding: 4px;
  font-size: 17px;
  font-family: Arial, Helvetica, sans-serif;
  cursor: pointer;
  border-radius: 3px;
  border-color: lightgrey;
}

.home button:hover {
  background-color: #45b244;
}

/* Product Details */
.details {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1rem;
}

.details-image {
  flex: 0.5 1 60rem;
}

.details-image img {
  max-width: 60rem;
  widows: 100%;
}

.details-info {
  flex: 1 1 30rem;
  font-size: 17px;
}

.details-action {
  flex: 1 1 30rem;
  font-size: 16px;
}

.details-info ul,
.details-action ul {
  list-style-type: none;
  padding: 0;
}

.details-info li,
.details-action li {
  margin-bottom: 1rem;
}

.back-to-result {
  padding: 3rem;
  cursor: pointer;
  font-size: 20px;
}

.details-action {
  border: 0.1rem #808080 solid;
  border-radius: 0.5rem;
  padding: 1rem;
}

.details-action ul li:last-child {
  display: flex;
  flex-direction: column;
}

.button {
  padding: 1rem;
  border: 0.1rem #808080 solid;
  cursor: pointer;
  border-radius: 0.5rem;
  background-color: palegreen;
}

.button:hover {
  border: 0.2rem #404040 solid;
}

/* Cart */
.cart {
  display: flex;
  flex-wrap: wrap;
  margin: 1rem;
  align-items: flex-end;
}

.cart h3 {
  font-size: 25px;
}

.price {
  font-size: 20px;
}

.cart-list {
  flex: 3 1 60rem;
}

.cart-action {
  flex: 1 1 20rem;
  border: 0.1rem #808080 solid;
  border-radius: 0.5rem;
  padding: 1rem;
}

.cart-list-container {
  padding: 0;
  list-style-type: none;
  padding: 1rem;
}

.cart-list-container li {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 1rem;
  border-bottom: 0.1rem #808080 solid;
}

.cart-list-container li img {
  max-width: 10rem;
  max-height: 10rem;
}

.cart-list-container li:first-child {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.cart-image {
  flex: 1 1;
}

.cart-name {
  flex: 8 1;
}

.cart-price {
  flex: 1 1;
  font-size: 2.5rem;
  text-align: right;
}

.cart-button {
  margin-left: 3rem;
  padding: 3px;
  font-size: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.cart-product-name {
  font-size: 20px;
}

.cart-product-qty {
  font-size: 17px;
}

